//Section 1
.section_first {
  height: 2000px;
  background-color: white;
  position: relative;
  padding-top: 50px;

  &::before {
    position: absolute;
    content: "";
    display: inline-block;
    height: 1vh;
    width: 100%;
    background-color: white;
    top: -2.5vh;
  }
}

.sf_header {
  text-align: center;
  font-size: 2.5em;
  color: rgb(255, 0, 84);
  transition: 1s;
  opacity: 0;
  margin-bottom: 50px;
}

.sf_container {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.sf_pic {
  height: 300px;
  width: 300px;
  background-color: pink;
  background-image: url("/img/cupCakes.jpg");
  background-size: contain;
  opacity: 0;
}

.sf_paragraph {
  height:300px;
  width: 300px;
  opacity: 0;
}

.sfh_land {
  @include land(1s);
}
